* {
  padding: 0;
  margin: 0; }

html {
  height: 100%;
  font-size: 10px; }

body {
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, Helvetica Neue, sans-serif;
  font-size: 1.4rem;
  line-height: 1.4;
  background-color: #efefef; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

p {
  margin: 10px 0 5px; }

h2 {
  text-align: center; }

form {
  padding: 10px;
  font-size: 1.6rem; }
  form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    font: inherit;
    box-sizing: border-box; }
    form input[type='checkbox'] {
      width: 20px;
      height: 20px;
      margin: 0 10px 0 0; }

form button {
  display: block;
  padding: 12px 22px;
  margin: 15px auto 0;
  font-size: inherit;
  color: #fbfbfb;
  cursor: pointer;
  background-color: #4a90e2;
  border: 0;
  border-radius: 5px;
  outline: 0;
  box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, 0.5); }
  form button:hover {
    background-color: #51a0fa; }

